.boosted-rev {
  padding: 45px 0;

  &__inner {
    padding-left: 10px;
    padding-right: 10px;
    display: flex;
    justify-content: space-between;
  }


  &__img-box{
    display: flex;



  }

  &__img {
    &:not(:last-child){
      margin-right: 20px;
    }
  }

  &__content {
    padding: 45px 0;
    margin-left: 40px;
  }

  &__title {
    font-weight: 400;
    font-size: 32px;
    line-height: 38px;
    margin-bottom: 21px;
  }

  &__text {
    font-weight: 400;
    font-size: 15px;
    line-height: 22px;
    max-width: 340px;
    margin-bottom: 18px;
  }

  &__btn {
    padding-left: 16px;
    padding-right: 16px;
  }

}

