.info {

  padding: 30px 0 20px;
  background-color: #FFFFFF;

  &__content {
    text-align: center;
    padding: 21px 18px 24px;
    margin-bottom: 20px;

  }

  &__title {
    margin: 0;
    padding: 0;
    font-weight: 400;
    font-size: clamp(1.25rem, 1.036rem + 1.07vw, 2rem);
    line-height: 36px;
    // text-align: center;
    color: #000000;
    margin-bottom: 17px;
    position: relative;
  }



  &__text-box{
    position: relative;
    &::after{
      content: "";
      position: absolute;
      width: 70px;
      height: 2px;
      bottom: 0;
      transform: translateX(-50%);
      background-image: url(../../img/line-gray.svg);
      background-repeat: no-repeat;
    }
  }



  &__text {
    padding: 0;
    margin: 0;
    text-align: center;
    font-weight: 400;
    font-size: clamp(0.75rem, 0.696rem + 0.27vw, 0.938rem);
    line-height: 19px;
    text-align: center;
    color: #000000;
    max-width: 685px;
    margin: 0 auto;
    padding-bottom: 25px;


  }

  &__items {
    padding: 0 10px;
    display: flex;
    gap: 0 20px;
  }

  &__item {
    width: 580px;
    height: 580px;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;


    &--left{
      background-image: url("../../img/info-1-bg.jpg");
      background-repeat: no-repeat;
      background-size: cover;
      background-position: center center;


    }
    &--right{
      background-image: url("../../img/info-2-bg.jpg");
      background-repeat: no-repeat;
      background-size: cover;
      background-position: center center;

    }
  }


  // &__link {
  // }

  &__item-content{
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;

  }
  &__item-icon{

    text-align: center;
    width: 29px;
    height: 47px;
    margin-bottom: 15px;

  }

  &__link-title {
    font-weight: 400;
    font-size: clamp(1.25rem, 1.161rem + 0.45vw, 1.563rem);
    line-height: 30px;
    text-align: center;
    color: #FFFFFF;
    margin: 0;
    margin-bottom: 15px;
    padding: 0;
    transition: all .5s all;
    // &:hover{
    //   color: var(--orange-color);
    // }
    @media (any-hover: hover) {
      &:hover {
        color: var(--orange-color);
      }
    }
  }

  &__link-text {
    text-align: center;
    font-weight: 400;
    font-size: 18px;
    line-height: 23px;
    text-align: center;
    color: #FFFFFF;
    transition: all .5s all;
    @media (any-hover: hover) {
      &:hover {
        color: var(--orange-color);
      }
    }
  }
}
