.footer-top {
  background-color: var(--mercury-color);

		&__inner {
      display: flex;
      padding: 50px 10px;

		}

    &__logo img{
      margin-right: 240px;
      flex: 0 0 auto;
    }

    &__logo-img{
      flex: 0 0 auto;
    }

		&__menu {
      margin-right: 20px;
      width: 380px;
		}

		&__title {
      font-weight: 400;
      font-size: 20px;
      line-height: 30px;
      color: var(--black-color);
		}

		// &__list {

		// }

		// &__list-item {
		// }

		&__list-link {
      font-weight: 400;
      font-size: 13px;
      line-height: 21px;
      color: var(--black-color);
		}

		&__content {
     
      max-width: 367px;
		}

		&__content-title {
      font-weight: 400;
      font-size: 20px;
      line-height: 30px;
      color: var(--black-color);

		}

		&__content-text {
      font-weight: 400;
      font-size: 14px;
      line-height: 21px;
      color: var(--black-color);
      margin-bottom: 20px;

		}

		// &__content-list {
		// }

		// &__content-listitem {
		// }

		&__content-listlink {
      font-weight: 400;
      font-size: 13px;
      line-height: 21px;
      color: var(--black-color);
		}
}



.footer-bottom {

		&__inner {
      padding: 20px 10px;
      display: flex;
      align-items: center;
      justify-content: space-between;
		}

		&__copyright {
      font-size: 13px;
      line-height: 19px;

      color: var(--black-color);
      span{
        font-weight: 400;
        font-size: 12px;
        line-height: 19px;
        color: var(--orange-color);
      }
		}

		&__pay-links {
      display: flex;
      gap: 4px;
		}

		// &__pay-link {

		// }

		// &__pay-icon {
		// }
}

