/* === DESKTOP FIRST === */

/* xl <= Extra extra large (xxl) */


@media (max-width : 1400px) {
  .shop__items{
    flex-direction: column;
  }
}



@media (max-width : 1230px) {
    .info__items{
      grid-template-columns: repeat(1, 1fr);
      gap: 20px;
     justify-content: center;
    }

}

/* lg <= Extra large (xl) */
@media (max-width : 1200px) {

  .burger{
    order: 2;
    // transform: translateY(0);
    margin-left: 30px;
    display: block;
    z-index: 2000;
  }

  .hero__btns{
    padding-top: 15px;
  }

  .hero__title,
  .boosted-rev__title,
  .accessories__title{
    margin-top: 0;

  }

  .nav{
    // display: none;
    position: fixed;
    top: 0;
    left: 0;
    right: 0;
    z-index: 1000;
    background-color: var(--orange-color);
    padding: 52px;
    transform: translateY(-100%);
    transition: all .3s ease-in-out;
  }

  .nav--visible{
    transform: none;
  }

  .menu--active{
    transform: none;
  }
  .nav__list{
    display: flex;
    flex-direction: column;
    align-items: center;
  }

  .nav__item:not(:last-child){
    margin: 0;
  }

  .nav__link{
    font-size: 18px;
    padding: 10px;
  }


  .boosted-rev__inner{
    padding-left: 0;
    padding-right: 0;
    flex-wrap: wrap-reverse;
    justify-content: center;
    align-items: center;

  }
  .boosted-rev__content{
    padding-top: 0;
    margin-left: 15px;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}
  .footer-top__inner{
    flex-wrap: wrap;
    justify-content: center;
  }
  // .boosted-rev__img--1{

  // }

/* md <= Large (lg) */
@media (max-width : 992px) {
  .info__items{
    flex-wrap: wrap;
    // gap:  20px 0;
   }

   .shop__items{
    flex-wrap: wrap;
   }
   .video__items{
    flex-wrap: wrap;
    justify-content: center;
    align-items: center;
   }
}


@media (max-width : 890px) {

    .beneffits__items{
      flex-wrap: wrap;
    }
    .video__inner{
      padding-right: 0;
    }
}


/* sm <= Medium (md) */
@media (max-width : 830px) {
  .boosted-rev__inner{
    flex-direction: column-reverse;
    display: flex;
    justify-content: center;
    align-items: center;
  }


  }
  .boosted-rev__img-box{
    align-items: center;
    justify-content: center;
    flex-wrap: wrap;
    gap: 20px;
  }

  .boosted-rev__img:not(:last-child){
    margin: 0;
  }

  .video__items{
    padding-right: 0;
  }


  .footer-top__inner{

    flex-wrap: wrap;

  }

  .footer-top__logo{
    margin: 0 ;
    padding: 0;
    margin-right: 0;
    display: inline-block;
    margin: 0 auto;
  }
  .footer-top__logo img{
    margin-right: 0;
  }

  .footer-top__title{
    margin: 20px 0;
  }
  .footer-top__list-item{
    padding: 7px 0;
  }
  .footer-top__menu{
    text-align: center;
    width: 100%;
    margin-bottom: 20px;
  }
  .footer-top__content{
    text-align: center;

  }

  .footer-bottom__inner{
    gap: 20px;
    flex-wrap: wrap;
    justify-content: center;
  }

  .footer-bottom__pay-links{
    flex-wrap: wrap;
  }



}

@media (max-width : 768px) {

    .video__items{
      padding: 0 15px;
    }
}

/* xs <= Small (sm) */
@media (max-width : 576px) {


}
@media (max-width : 475px) {

  .skateboards{
    background-position: left -35px  center;
  }

  .hero__btns{
    flex-wrap: wrap;
    gap: 40px;
  }

  .info__text {
    padding: 0;
    margin: 0;
    text-align: center;
    font-weight: 400;
    font-size: clamp(0.75rem, 0.696rem + 0.27vw, 0.938rem);
    line-height: 19px;
    text-align: center;
    color: #000000;
    max-width: 685px;
    margin: 0 auto;
    padding-bottom: 25px;
    position: relative;
    &::after{
      content: "";
      position: absolute;
      width: 72px;
      height: 2px;
      background-color: #CFCFCF;
      left: 40%;
      right: 60%;
      bottom: 0;
    }
  }
}



