.hero {
  background-image: url("../img/hero-bg.jpg");
  background-repeat: no-repeat;
  background-size: cover;
  background-position: center center;
  min-height: 100vh;

  &__inner {
    padding: 45px 0;
    text-align: center;
  }

  &__title {
    font-weight: 400;
    font-size: clamp(1.563rem, 1.223rem + 1.7vw, 2.75rem);
    line-height: 51px;
    text-align: center;
    color: #000000;
    margin-bottom: 14px;
  }

  &__text {
    font-weight: 400;
    font-size: clamp(0.625rem, 0.536rem + 0.45vw, 0.938rem);
    line-height: 19px;

    text-align: center;
    letter-spacing: 1px;

    color: #000000;
    margin-bottom: 22px;
  }

  &__btns {
    display: flex;
    justify-content: center;
    gap: 12px;
  }

  &__btn {
    white-space: nowrap;
  }

  &__btn--left {
    padding-left: 47px;
    padding-right: 47px;
  }

  &__btn--right {
    padding-left: 37px;
    padding-right: 37px;
  }
}
