.shop {

  &__items {
    display: flex;
      justify-content:space-between;
      // width: 100%;

  }

  &__item {
    width: 480px;
    height: 480px;
    width: 100%;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    &--1{
        background-image: url("../../img/shop-1-bg.jpg");
        background-repeat: no-repeat;
        background-size: cover;
        background-position: center;
    }

    &--2{
      background-image: url("../../img/shop-2-bg.jpg");
      background-repeat: no-repeat;
      background-size: cover;
         background-position: center;
    }

    &--3{
      background-image: url("../../img/shop-3-bg.jpg");
      background-repeat: no-repeat;
      background-size: cover;
         background-position: center;
    }

  }

  &__link-title {
    font-weight: 400;
    font-size: 25px;
    line-height: 30px;
    text-align: center;
    color: #FFFFFF;
    padding: 0;
    margin: 0;
    margin-bottom: 15px;
    transition: all .5s all;

    @media (any-hover: hover) {
      &:hover {
        color: var(--orange-color);
      }
    }

  }

  &__link-text {
    font-weight: 400;
    font-size: 18px;
    line-height: 23px;
    text-align: center;
    color: #FFFFFF;
    text-transform: uppercase;
    transition: all .5s all;
    @media (any-hover: hover) {
      &:hover {
        color: var(--orange-color);
      }
    }


  }


}
