.header-top {
padding: 7px 0;
background-color: var(--black-color);
color: var(--white-color);

		&__text {
      text-align: center;
      font-size: clamp(0.875rem, 0.839rem + 0.18vw, 1rem);
		}
}
.header-bottom {
  display: flex;
  align-items: center;
  padding: 15px 20px 15px 11px;

}
.header-logo {
  margin-right: 15px;
  flex: 0 0 auto;
		// &__img {

		// }
}

.nav {

		&__list {
      display: flex;
      gap: 23px;
		}

		// &__item {
		// }

		&__link {
      font-family: 'Roboto';
      font-style: normal;
      font-weight: 400;
      font-size: 14px;
      line-height: 21px;
      letter-spacing: 1px;
      text-transform: uppercase;
      color: var(--black-color);
      white-space: nowrap;
		}
}

.user-nav {
  display: flex;
  gap: 10px;
  margin-left: auto;

		&__link {
      flex: 0 0 auto;
		}

		&__link-icon {
      flex: 0 0 auto;
		}
}

.burger{
  @include burger;
  display: none;
  // transform: translateY(-250px);
}
