.accessories {
padding-bottom: 80px;
padding-left: 10px;
padding-right: 10px;
  &__title {
    text-align: center;
    padding-bottom: 25px;
    position: relative;
    &::after{
      content: "";
      position: absolute;
      width: 70px;
      height: 2px;
      bottom: 0;
      transform: translateX(-250%);

      background-image: url("../../img/line.svg");
      background-repeat: no-repeat;
    }

  }
}


.accessories-slider {
  padding: 40px 10px 80px;


  &__items {
    display: flex;
    // justify-content: space-between;
    gap: 20px;

  }

  &__item {
    max-width: 185px;
    min-height: 255px;
    max-width: 100%;
  }


  &__item-top {
    width: 186px;
    height: 186px;
    margin: 0 auto;
    padding: 10px;
  }

  &__item-topimg {
    object-fit: cover;
  }

  &__item-bottom {
      max-width: 185px;
  //   text-align: center;
    min-height: 70px;
    padding-top: 12px;
    padding-left: 8px;
    padding-right: 10px;
    margin: 0 auto;


  }

  &__item-bottomname {
    font-weight: 400;
    font-size: 15px;
    line-height: 18px;
    text-align: center;
    color: var(--orange-color);
    padding: 0;
    margin: 0;
  }


  &__item-bottomprices {
    display: flex;
    align-items: center;
    justify-content: center;
    padding: 0;
    margin: 0;

  }

  &__item-bottompricenew {
    font-weight: 400;
    font-size: 14px;
    line-height: 18px;
    color: #373737;
    padding: 0;
    margin: 0;

  }


  &__item-bottompriceold {
    font-weight: 400;
    font-size: 14px;
    line-height: 18px;
    color: #FF2121;

    padding: 0;
    margin: 0;
    margin-left: 4px;
  }


  &__item-bottompricesoldout {
    font-weight: 400;
    font-size: 15px;
    line-height: 18px;
    text-align: center;
    color: rgba(0, 0, 0, 0.3);
    padding: 0;
    margin: 0;
  }



.slick-arrow{
  border: none;
  outline: none;
  background-color: #FFFFFF;
  position: absolute;
  z-index: 5;
  transform: translateY(-50%);
  cursor: pointer;
  top: 50%;
  width: 44px;
  height: 44px;
  border-radius: 50%;

}

.slick-prev{
  left: 20px;
}
.slick-next{
  right: 20px;
}


.slick-dots{
  padding-left: 0;
  list-style: none;
  display: flex;
  justify-content: center;
  align-items: center;
  position: absolute;
  bottom: -70px;
  right: 0;
  left: 0;
}



.slick-dots button{
  padding: 0;
  cursor: pointer;
  width: 10px;
  height: 10px;
  background-color: #C4C4C4;
  border-radius: 50%;
  font-size: 0;
  border: none;
}
.slick-dots li + li{
  margin-left: 15px;
}
.slick-active button {
  background-color: var(--orange-color);
}



}

