.beneffits {
  background-color: var(--black-color);
  padding: 15px 30px 35px;


  &__items {
    display: flex;
    justify-content: space-evenly;
    gap: 0 20px;
  }

  &__item {

    width: 330px;
    height: 257px;
    text-align: center;
    padding: 20px;
  }

  &__item-icon {
    padding: 0;
    margin: 0;
    margin-bottom: 5px;
  }

  &__item-title {
    padding: 0;
    margin: 0;
    font-weight: 700;
    font-size: clamp(1.563rem, 1.455rem + 0.54vw, 1.938rem);
    line-height: 36px;
    text-align: center;
    color: #FFFFFF;
  }

  &__item-text {
    font-weight: 400;
    font-size: clamp(0.75rem, 0.696rem + 0.27vw, 0.938rem);
    line-height: 22px;
    text-align: center;
    color: #FFFFFF;
  }
}

