.skateboards {

    background-image: url("../../img/skateboards-bg.jpg");
    background-repeat: no-repeat;
    background-size:  cover;
    background-position: left   center;
    min-height: 100vh;

  &__content {
    padding-top: 30px;
    display: flex;
    flex-direction: column;
    align-items: center;
  }

  &__descr {

      font-weight: 400;
      font-size: 15px;
      line-height: 19px;
      text-align: center;
      letter-spacing: 1px;
      color: #000000;
  }

  &__title {
    text-align: center;
    font-weight: 700;
    font-size: clamp(1.75rem, 1.446rem + 1.52vw, 2.813rem);
    line-height: 51px;
    text-align: center;
    padding: 0;
    margin: 0;

  }

  &__text {
    font-weight: 400;
    font-size: clamp(0.75rem, 0.696rem + 0.27vw, 0.938rem);
    line-height: 19px;
    text-align: center;
    letter-spacing: 1px;
    color: #000000;
    max-width: 488px;

  }



  &__btn {
    display: inline-block;
    padding-left: 66px;
    padding-right: 66px;

  }


}

